import React, { Component, Fragment } from "react";
import Navbar from '../../components/Navbar/Navbar'
import { MDBContainer } from "mdbreact";
import Multislider from '../../components/Multislider/Multislider';
import Footer from '../../components/Footer/Footer'
import './Productpage.css';
import ScrollUpButton from "react-scroll-up-button"; //Add this line Here
import { isMobile, isMobileOnly, isTablet } from 'react-device-detect';
import Product from '../../components/Product/Product';

class ProductPage extends Component {

    showtab(cityName) {
        console.log("sds")
        var i, tabcontent, tablinks;
        tabcontent = document.getElementsByClassName("tabcontent");
        for (i = 0; i < tabcontent.length; i++) {
            tabcontent[i].style.display = "none";
        }
        tablinks = document.getElementsByClassName("tablinks");
        for (i = 0; i < tablinks.length; i++) {
            tablinks[i].className = tablinks[i].className.replace(" active", "");
        }
        document.getElementById(cityName).style.display = "block";

    }
    render() {
        return (

            <Fragment>

                <ScrollUpButton />

                <Navbar></Navbar>


                <MDBContainer fluid className="home">

                    <div style={{padding:"10px"}}>
                    <Product></Product>

                    </div>

                    <div style={{marginTop:"50px"}}>

                        <div className="col-md-12">

                            <div className="tab">
                                <button className="tablinks active" onClick={() => this.showtab('itemdesc')}>Item Description</button>
                                <button className="tablinks" onClick={() => this.showtab('reviews')}>Customer Reviews</button>
                                <button className="tablinks" onClick={() => this.showtab('shoppingtime')}>Shipping Time and Cost</button>
                            </div>

                            <div id="itemdesc" style={{ display: "block" }} className="tabcontent">
                                <h3>Item Description</h3>
                                <p>Item Description of this Product.</p>
                            </div>

                            <div id="reviews" className="tabcontent">
                                <h3>Customer Reviews</h3>
                                <p>Customer Reviews for this Product.</p>
                            </div>

                            <div id="shoppingtime" className="tabcontent">
                                <h3>Shipping Time and Cost</h3>
                                <p>Shipping Time and Cost for this Product.</p>
                            </div>

                        </div>

                    </div>
                    {/* Customers Who Bought This Item Also Bought */}
                    <div style={{ marginTop: "100px" }}>
                        <h3 style={{ margin: "5px" }}>Customers Who Bought This Item Also Bought</h3>

                        <div style={{ background: "white" }}>

                            <Multislider sponsers={false} picheight={"150px"} height={"250px"} width={"200px"} numbers={4}>

                            </Multislider>
                        </div>
                    </div>
                    {/* Similar Items */}
                    <div style={{ marginTop: "100px" }}>
                        <h3 style={{ margin: "5px" }}>Similar Items</h3>

                        <div style={{ background: "white" }}>

                            <Multislider sponsers={false} picheight={"150px"} height={"250px"} width={"200px"} numbers={4}>

                            </Multislider>
                        </div>
                    </div>


                </MDBContainer>

                <Footer></Footer>
            </Fragment >

        );
    }
}

export default ProductPage;